.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.App-header {
  background: linear-gradient(to right, #5D5CDE, #7AADE3);
  padding: 20px;
  color: white;
  border-radius: 10px;
  margin-bottom: 20px;
  position: relative;
}

.App-header h1 {
  margin: 0;
}

.back-button {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(255, 255, 255, 0.3);
  border: none;
  color: white;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.home-container {
  max-width: 800px;
  margin: 0 auto;
}

.test-options {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 40px;
}

.test-card {
  background-color: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 10px;
  padding: 20px;
  width: 300px;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
}

.test-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

/* Test specific styles */
.test-container {
  max-width: 800px;
  margin: 0 auto;
  background-color: #f8f9fa;
  border-radius: 10px;
  padding: 20px;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

.question-container {
  text-align: left;
  margin-bottom: 30px;
}

.options-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin: 15px 0;
}

.option-button {
  padding: 10px;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s;
}

.option-button.selected {
  background-color: rgba(93, 92, 222, 0.1);
  border-color: #5D5CDE;
}

.submit-button {
  background-color: #5D5CDE;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  margin-top: 20px;
  cursor: pointer;
}

.submit-button:disabled {
  background-color: #B0B0B0;
  cursor: not-allowed;
}

.test-description {
  margin-bottom: 30px;
}

/* Results styling */
.results-container {
  max-width: 800px;
  margin: 0 auto;
  background-color: #f8f9fa;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
}

.score-summary {
  margin: 30px 0;
}

.score-circle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #5D5CDE;
  color: white;
  font-size: 32px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 15px;
}

.metrics-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin: 30px 0;
}

.metric {
  text-align: center;
}

.metric-bar {
  height: 12px;
  background-color: #e9ecef;
  border-radius: 6px;
  margin: 10px 0;
  overflow: hidden;
}

.metric-fill {
  height: 100%;
  background-color: #5D5CDE;
  border-radius: 6px;
}

.analysis-box {
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  margin: 20px 0 30px;
  text-align: left;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

/* Camera and Hand Test styles */
.video-container {
  position: relative;
  width: 100%;
  max-width: 640px;
  margin: 0 auto;
  border: 1px solid #ced4da;
  border-radius: 10px;
  overflow: hidden;
}

.camera-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.3);
}

.hand-target {
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: rgba(93, 92, 222, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 24px;
  cursor: pointer;
  transition: transform 0.1s ease, background-color 0.1s ease;
}

.hand-target:hover {
  transform: scale(1.1);
  background-color: rgba(93, 92, 222, 0.9);
}

.camera-permission {
  text-align: center;
  padding: 30px;
  background-color: #f8f9fa;
  border-radius: 8px;
  margin: 20px 0;
}

.test-active {
  text-align: center;
}

.test-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  margin-top: 20px;
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 15px;
}

.test-stat {
  text-align: center;
}

.test-stat-label {
  font-size: 14px;
  color: #6c757d;
}

.test-stat-value {
  font-size: 18px;
  font-weight: bold;
  color: #5D5CDE;
}

/* Hand reaction results styling */
.metric-value {
  font-size: 24px;
  font-weight: bold;
  color: #5D5CDE;
  margin: 10px 0;
}

.reaction-times-container {
  margin: 30px 0;
  text-align: center;
}

.reaction-times-chart {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  height: 120px;
  margin: 20px 0;
  padding: 10px;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.reaction-time-bar {
  width: 40px;
  min-height: 20px;
  border-radius: 4px 4px 0 0;
  position: relative;
}

.reaction-time-label {
  position: absolute;
  bottom: -25px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 12px;
  white-space: nowrap;
}

.analysis-detail {
  margin-top: 15px;
  font-size: 14px;
  color: #6c757d;
}

/* Hand detection styles */
.hand-keypoint {
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  z-index: 10;
  pointer-events: none;
}

.hand-tips {
  margin-top: 20px;
  padding: 15px;
  background-color: #f8f9fa;
  border-radius: 8px;
  text-align: left;
}

.hand-tips ul {
  list-style-type: disc;
  padding-left: 20px;
  margin-top: 10px;
}

.hand-tips li {
  margin-bottom: 5px;
}

.debug-info {
  margin-top: 10px;
  font-size: 12px;
  color: #6c757d;
  text-align: center;
}

/* Debug mode styling */
.debug-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
}

.debug-button {
  background-color: #f8f9fa;
  border: 1px solid #ced4da;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
}

.debug-log {
  margin-top: 15px;
  padding: 10px;
  background-color: #f8f9fa;
  border-radius: 5px;
  border: 1px solid #ced4da;
  max-height: 200px;
  overflow-y: auto;
  text-align: left;
}

.debug-log h4 {
  font-size: 14px;
  margin-bottom: 5px;
}

.debug-log ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-family: monospace;
  font-size: 12px;
}

.debug-log li {
  margin-bottom: 2px;
  line-height: 1.3;
}

/* Demo mode styling */
.mode-toggles {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-bottom: 10px;
}

.debug-button, .demo-button {
  background-color: #f8f9fa;
  border: 1px solid #ced4da;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
}

.demo-button {
  background-color: #e6f7ff;
  border-color: #91d5ff;
}

.demo-banner {
  background-color: #e6f7ff;
  border: 1px solid #91d5ff;
  border-radius: 4px;
  padding: 8px;
  margin-bottom: 15px;
  text-align: center;
  color: #0050b3;
}

.demo-note {
  color: #0050b3;
  font-weight: bold;
}

