  overflow-y: auto;
  text-align: left;
}

.debug-log h4 {
  font-size: 14px;
  margin-bottom: 5px;
}

.debug-log ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-family: monospace;
  font-size: 12px;
}

.debug-log li {
  margin-bottom: 2px;
  line-height: 1.3;
}

.tech-explanation {
  margin-top: 20px;
  padding: 15px;
  background-color: #f8f9fa;
  border-radius: 8px;
  text-align: left;
}

.tech-image {
  max-width: 100%;
  height: auto;
  margin-top: 10px;
  border-radius: 4px;
}

/* Detection visualization */
.detection-area {
  position: absolute;
  border: 2px dashed rgba(75, 192, 192, 0.5);
  border-radius: 50%;
  pointer-events: none;
}

.detection-indicator {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 14px;
}

.test-stat-value.detected {
  color: #52c41a;
  font-weight: bold;
}

/* Demo hand styling */
.demo-hand {
  position: absolute;
  font-size: 36px;
  transform: translate(-50%, -50%);
  filter: drop-shadow(0 0 5px rgba(93, 92, 222, 0.7));
  z-index: 10;
  pointer-events: none;
}

