/* Eye Tracking Test Styles */
.eye-target {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: rgba(93, 92, 222, 0.7);
  transform: translate(-50%, -50%);
  box-shadow: 0 0 10px rgba(93, 92, 222, 0.5);
  z-index: 5;
  cursor: pointer;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { transform: translate(-50%, -50%) scale(1); }
  50% { transform: translate(-50%, -50%) scale(1.1); }
  100% { transform: translate(-50%, -50%) scale(1); }
}

.eye-position-indicator {
  position: absolute;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: rgba(255, 0, 0, 0.7);
  transform: translate(-50%, -50%);
  z-index: 10;
  pointer-events: none;
}

.eye-target {
  position: absolute;
  width: 50px; /* Larger size */
  height: 50px;
  border-radius: 50%;
  background-color: rgba(255, 0, 0, 0.8); /* Brighter color */
  transform: translate(-50%, -50%);
  box-shadow: 0 0 15px rgba(255, 0, 0, 0.8); /* More visible glow */
  z-index: 15; /* Ensure it's on top */
  cursor: pointer;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { transform: translate(-50%, -50%) scale(1); }
  50% { transform: translate(-50%, -50%) scale(1.2); }
  100% { transform: translate(-50%, -50%) scale(1); }
}